body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.planet_infos__wrap {
    padding: 30px;

    display: flex;
    align-items: center;
    flex-direction: column;
}

    .planet_infos_list li {
        color: var(--cor-amarelo);
        font-family: "Roboto";
        font-size: 24px;
        font-weight: bold;
        line-height: 30px;
        text-transform: uppercase;
        width: 100%;
    }

        .planet_infos_list li span {
            font-style: italic;
        }

    .planet_infos_films {
        color: var(--cor-amarelo);
        font-family: "Roboto";
        font-size: 20px;
        margin-top: 30px;
        text-transform: uppercase;
    }

        .planet_infos_films span {
            font-weight: bold;
        }
.planet__header {
    border-bottom: 4px solid var(--cor-amarelo);
    padding: 30px;
}

    .planet__header_name {
        color: var(--cor-amarelo);
        font-family: "STARWARS";
        font-size: 45px;
        font-weight: bold;
        letter-spacing: 5px;
        margin-bottom: 0;
        text-align: center;
        text-transform: uppercase;
    }
/*font*/
@font-face {
    font-family:"STARWARS";
    src:url(/static/media/STARWARS.fa7a31d2.eot) format("eot"),
    url(/static/media/STARWARS.35f31c7a.woff) format("woff"),
    url(/static/media/STARWARS.3f085be9.ttf) format("truetype"),
    url(/static/media/STARWARS.95e43dd6.svg#STARWARS) format("svg");
    font-weight:normal;
    font-style:normal;
}

* {
    border: none;
    font-family: Roboto, Arial, Verdana, sans-serif;
    font-size: 100%;
    font-weight: normal;
    margin: 0;
    padding: 0;
    webkit-box-sizing: border-box;
    moz-box-sizing: border-box;
    box-sizing: border-box; 
}

html,
body,
main,
header,
footer {
    width: 100%; 
}

li {
    list-style: none;
}

:root {
    /*variáveis*/
    --cor-amarelo: #FFFF82;
}

body {
    background-image: url(/static/media/stars.fb2d193e.jpg);
    background-size: cover;
    height: 100%;
    padding: 5%;
    position: relative;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width: 700px) {
    body {
        padding: 0;
    }
}


    .root__wrap {
        height: 100%;
        width: 100%;

        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
    }

        .planet__wrap {
            background-color: rgba(255, 255, 255, 0.3);
            border: 4px solid #FFFF82;
            border: 4px solid var(--cor-amarelo);
            height: 80%;
            width: 60%;

            display: flex;
            align-items: center;
            flex-direction: column;

            -webkit-transform: perspective(300px) rotateX(5deg);

                    transform: perspective(300px) rotateX(5deg);
            -webkit-transform-origin: 50% 100%;
                    transform-origin: 50% 100%;
        }

            .planet__wrap > div {
                width: 100%;
            }


        .planet_next__button {
            background: transparent;
            border: 3px solid #FFFF82;
            border: 3px solid var(--cor-amarelo);
            cursor: pointer;
            font-family: "STARWARS";
            font-size: 20px;
            margin-top: 30px;
            padding: 10px 20px;
            text-transform: uppercase;
        }

        .planet_next__button:hover {
            opacity: 0.7;
        }





